/*
.userTag    用户名称显示的黄色长条
.seat       在里面显示为换色，这两个div 可以省略一个。
.user
*/
.board > .userTag{
    position: absolute;
    /* background-color: #eeeeee; */
    height:10%;
}
.board .seat {
    text-align:center;
    /* background-color: #ffff55; */
}
.board > .userTag:nth-child(1) > .seat{
         height:100%;
}
.board > .userTag:nth-child(2) > .seat{
    height:100%;      
}
.board > .userTag:nth-child(3) > .seat{
         height:100%; 
}
.board > .userTag:nth-child(4) > .seat{
    height:100%;      
}
.board > .userTag:nth-child(1){
    right:-35%;   width:80%;    top:45%;   -webkit-transform: rotate(-90deg);   transform: rotate(-90deg)
}
.board > .userTag:nth-child(2){
    bottom:0px;   width:100%;    left:0%;
}
.board > .userTag:nth-child(3){
    left:-35%;    width:80%;    top:45%;    -webkit-transform: rotate(90deg);    transform: rotate(90deg)
}
.board > .userTag:nth-child(4){
    width:100%;    left:0%;
}


